.family-onboarding-header {
  display: none;
}

.family-onboarding-header-image {
  display: none;
}

.family-onboarding-final-header-image {
  display: none;
}

.omar-override {

  font-family: $font-default;
  @include rounded(3px);

  .buttons {
    margin-top: 20px;
    float: right;
  }

  .btn-base {
    height: 36px;
    @include rounded(18px);
    @include shadow( 0 1px 1px rgba($gray-shade-7, 0.1));
    font-weight: $weight-semibold;
    font-size: 11px;
    letter-spacing: -0.3px;
    line-height: 1.6;

    margin: 6px;
    padding: 6px 24px;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
    @include transition(box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1));
  }

  .btn-primary {
    @include shadow( 0 1px 1px rgba($gray-shade-7, 0.1), 0 1px 6px rgba($gray-shade-7, 0.2));
  }

  .btn-link {
    color: $gray-shade-4;
    font-weight: $weight-semibold;
    border-color: $transparent;
    @include shadow(none);
  }
}
