
.omar-migration-header-image {
  display: none;
}

.read-more {
  display: none;
}

.intro-tour-dialog.omar-migration-override {
  border-radius: 10px;
}

.omar-migration-override {
  &.migration-first-step {
    border-radius: 4px;
    width: 430px;
    height: 384px;
    position: fixed;
    margin: 0 auto;
    left: 0!important;
    right: 0;
    top: calc(100% - 85%)!important;

    @media (min-height: 500px) {
      top: calc(100% - 75%)!important;
    }

    p {
      color: #000000;
    }

    .btn-primary {
      width: 180px;
      font-size: 11px;
    }

    .btn-base {
      font-size: 11px;
    }

    #contentText1 {
      margin-bottom: 12px;
      font-size: 14px;
    }

    .btn-close {
      display: none;
    }

    .intro-tour {
      padding: 24px 24px 0;
      margin: 0;
    }

    .omar-migration-header-image {
      display: block;
      height: 186px;
    }

    #mainTitle {
      display: block;
      font-size: 18px;
      color: #646d73;
      line-height: 1;
      margin: 0 0 18px;
      width: 100%;
      font-weight: 500;
    }
  }
  &.migration-second-step {
    max-height: 160px!important;
    height: 160px;

    #contentText1 {
      margin-bottom: 12px;
      font-size: 14px;
    }

    #contentText2 {
      display: inline-block!important;
      padding-right: 0;
      font-size: 14px;
    }

    .read-more {
      display: inline-block;
      color: #3598db;
      text-decoration: none;
      padding-left: 6px;
    }

    #btnClose {
      display: none;
    }

    p {
      color: #000000;
    }

    .btn-primary {
      font-size: 11px;
      width: 120px;
    }

    .buttons {
      margin-top: 12px;
      padding: 0 12px 0 0;

      .col-6 {
        width: 100%;
        text-align: right;
      }
    }
  }
  &.migration-last-step {
    .btn-primary {
      min-width: 120px;
    }
  }

  .btn-base {
    background-color: #ffffff;
    border: 1px solid #e7e8ea;
    border-radius: 50px;
    box-shadow: 0 1px 1px rgba(59, 74, 84, 0.1);
    color: #646d73;
    display: inline-block;
    font-weight: 600;
    font-size: 12px;
    letter-spacing: 0.13px;
    line-height: 1.6;
    height: 36px;
    margin: 6px;
    padding: 6px 24px;
    outline: 0;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    cursor: pointer;
    -webkit-transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
    -moz-transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
    -ms-transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
    -o-transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
    transition: box-shadow 225ms cubic-bezier(0.25, 0.8, 0.25, 1), border-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1), background-color 225ms cubic-bezier(0.25, 0.8, 0.25, 1);
  }

  .btn-primary {
    color: #ffffff;
    background-color: #d32d27;
    border-color: #d32d27;
    box-shadow: 0 1px 1px rgba(59, 74, 84, 0.1), 0 1px 6px rgba(59, 74, 84, 0.2);

    &:hover {
      border-color: #AF0809;
    }
  }

  .btn-link {
    color: #646d73;
    font-weight: 600;
    border-color: transparent;
    background-color: transparent;
    box-shadow: none;
  }

  .buttons {
    float: right;
    margin-top: 20px;
    width: 100%;
    padding-left: 95px;
  }
}



