@import '../styles/variables';
@import '../styles/mixins';
@import '../styles/utility';
@import '../styles/arrow';
@import '../styles/buttons';
@import '../styles/ping';
// In the future we must separate all the Tour specific items into separate files
@import "./FamilyOnboarding/familyOnboardingTour";
@import "./OmarVaultTour/omarVaultTour";
@import "./OmarMigrationTour/omarMigrationTour";

$width-default: 430px;

.intro-tour-dialog {
  width: $width-default;
  margin: inherit;
  background-color: $white;

  .dialogContent {
    padding: 0;
    overflow: hidden;
    position: relative;

    #contentText1,
    #contentText2 {
      padding-right: 12px;
    }
  }

  &.tour-welcome-step {
    box-shadow: none;
    bottom: initial;
    z-index: 3;

    .intro-tour {
      width: calc(100% - 70px);
      padding-left: 50px;
    }
  }
}

.intro-tour {
  margin-top: 15px;
  margin-bottom: 15px;
  width: $width-default - 30px;
  color: $gray-shade-5;

  @include font-sm();

  h1 {
    @include font-xm();
    margin-bottom: $margin-sm;
    margin-top: $margin-sm;
  }

  .main-icon {
    width: 75px;
    height: 75px;
  }

  .btn-close {
    width: 12px;
    height: 12px;
    cursor: pointer;
  }
  .buttons {
    padding-left: 0;
  }
}

.tour-overlay {
  background-color: rgba($gray-shade-5, .3);

  .tour {
    opacity: .3;
  }
}

.empty .tour-overrides #main {
  top: 215px;
}

.tour-overrides {
  .middlePosition {
    top: 235px;
  }
  #tools, #ad {
    &.middlePosition {
      top: 60px;
    }
  }
  #main {
    top: 300px;
  }
  .bottom {
    top: 295px;
  }
  .emptyVault {
    top: -155px;
    overflow: hidden;
  }

  #emptySites,
  #emptyNotes,
  #emptyFormFills,
  #emptySentShares,
  #emptySharedFolders,
  #emptyReceivedShares,
  #emptyEmergencyTrusted,
  #emptyEmergencyTrusting,
  #emptyCreditMonitoring
  {
    .emptyVault {
      top: 0;

      .tour {
        margin-top: 0;
        top: 0;
      }
    }
  }
}

.invisible-shade {
  opacity: 0;
  z-index: 7;
  position: absolute;
  width: 100%;
  height: 100%;
}
.dark-shade {
  z-index: 2;
  background-color: rgba(52, 61, 68, 0.6);
  position: absolute;
  top: 60px;
  bottom: 0;
  left: 60px;
  right: 0;
}

.newFlag {
  position: absolute;
  right: 10px;
  top: 8px;
  padding: 6px 7px;
  border-radius: 25px;
  color: $gray-tint-05;
  background-color: $gray-shade-6;
  font-size: $font-size-xxs;
  line-height: 1;
  text-transform: uppercase;

  &.active {
    color: $gray-shade-6;
    background-color: $white;
  }
}

.leftMenuItem {
  &.newFlag.active {
    color: $gray-shade-6;
    background-color: $white;
  }
}